.el-timeline-item {
  position: relative;
  padding-bottom: 20px; }
  .el-timeline-item__wrapper {
    position: relative;
    padding-left: 28px;
    top: -3px; }
  .el-timeline-item__tail {
    position: absolute;
    left: 4px;
    height: 100%;
    border-left: 2px solid #e4e7ed; }
  .el-timeline-item__icon {
    color: #ffffff;
    font-size: 13px; }
  .el-timeline-item__node {
